@import url(https://fonts.googleapis.com/css2?family=Germania+One&display=swap);
*{box-sizing:border-box}html,body,#root{height:100%}body{margin:0}.home{width:100%;height:100%}.home .background{width:100%;height:100%;background-image:url(/alexredwood/router/static/media/background.fe62e064.webp);background-repeat:no-repeat;background-size:cover;background-position:center;filter:grayscale(100%);-webkit-filter:grayscale(100%);position:fixed;left:0;right:0;z-index:1;display:block}.home .title-container{width:100%;height:100%;position:fixed;z-index:9999;display:flex}.home .title-container a{color:#bb2205;text-decoration:none;background-color:rgba(0,0,0,0.7);padding:60px 100px;margin:auto;max-width:900px;cursor:pointer;transition:background-color 0.4s ease;border-radius:0;border:4px solid #bb2205}@media (max-width: 576px){.home .title-container a{max-width:900px;padding:50px 80px}}@media (max-width: 390px){.home .title-container a{max-width:900px;padding:40px 40px}}.home .title-container a:hover{background-color:rgba(0,0,0,0.8)}.home .title-container a h1{color:#bb2205;font-family:"Germania One", cursive;font-size:3rem;word-wrap:nowrap}@media (max-width: 576px){.home .title-container a h1{font-size:2rem}}@media (max-width: 390px){.home .title-container a h1{font-size:1.6rem}}.home .title-container a h1:hover{text-decoration:none;color:#bb2205}.navbar-light .navbar-brand{font-family:"Germania One", cursive;color:#bb2205 !important;font-size:1.8rem;text-shadow:2px 2px 0px black;letter-spacing:1px}@media (max-width: 390px){.navbar-light .navbar-brand{font-size:1.6rem}}.nav-item{font-size:1rem}.fas{font-size:1.6rem}#toggle-btn{align-self:right}

